input {
  border-radius: 3px;
}

.mainContent{
display: flex;
flex-direction: column;
justify-content: space-around;
  font: inherit;
  margin: 100px;
  color: #391400;
  font-size: 16px;
    line-height: 32px;
    color: #391400;
    opacity: 84%;
}


.parameter{
  padding: 50px;
  align-items: center;
  vertical-align: middle;
  height: 20px;
  line-height: 20px;
  font-size: 20px;
  border: 5px solid rgba(211, 154, 154, 0.348);
      border-top: 5px solid rgb(211, 154, 154);
      border-right: 5px solid rgb(211, 154, 154);
      color: #391400;
  margin-bottom: 5px;
  border-radius: 10px;
  width: 600px;

  display: flex;
  
}
.parameter:first-child {
    border-top: 5px solid rgb(211, 154, 154);
}
.parameter:last-child {
    margin-bottom: 150px;
    border: none;


}

.parameterName{
  margin-right: 10px;
  width: 400px;
  color: #391400;
}
.info{
  font-size: smaller;
}

.parameterSave{
    padding: 0;
    border: none;
    font: inherit;
    color: inherit;
    background-color: transparent;
    cursor: pointer;
    border: 1px solid black;
    border-radius: 5px;
    padding: 10px 50px;
    background-color: #fff;
        opacity: 84%;

}
