input{border-radius: 3px;}

.cardsList {
  display: flex;
  flex-wrap: wrap;
}

.card {
  display: inline-block;

  width: 700px;
  border: 6px solid rgba(171, 111, 106, 0.279);
  padding: 3px;
  /* margin-bottom: 30px; */
  margin: 0 15px 30px 15px;
  border-radius: 19px;
  display: flex;
  justify-content:space-between;
}


.infoList{
  display: flex;
  flex-direction: column;
  /* align-items: flex-start; */
}
                                                   


input{
  display: inline-block;
  width: min-content;
  margin: 0;padding: 0;
  margin-right: 10px;
}




.cardImgWrapper {
  width: 400px;
  height: 400px;
  overflow: hidden;
  align-items: center;
  border-radius: 19px;
}

.cardImg {
  min-height: 100%;
    min-width: 100%;
    width: auto;
    height: auto;
    display: block;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) ;
    opacity: 0.8;
}





.cardCategory {
  color: #391400;
  font-size: 16px;
  /* font-family: Epilogue-Regular; */
  line-height: 20px;

height: 50px;
  background-color: #e082823e;
  border-radius: 19px;
  padding: 0 19px;
margin: 5px;

  
}

